<template>
  <div>
    <v-dialog
      v-model="showDialog"
      persistent
      :fullscreen="isXs"
      max-width="600"
    >
      <v-card>
        <v-layout
          row
          wrap
        >
          <v-flex xs12>
            <v-layout
              row
              wrap
            >
              <v-flex
                sm4
                xs6
              >
                <v-radio-group
                  label=""
                  v-model="seatType"
                  :row="!isXs"
                  class="body-1"
                >
                  <v-radio
                    v-for="seatType in seatTypes"
                    :label="seatType"
                    :value="seatType"
                    :key="seatType"
                    color="brown lighten-2"
                  ></v-radio>
                </v-radio-group>
                <v-radio-group
                  v-model="weightAndSize"
                  :mandatory="false"
                >
                  <v-radio
                    v-for="aAeightAndSize in weightAndSizes"
                    :label="getNameOf(aAeightAndSize)"
                    :value="aAeightAndSize"
                    :key="getNameOf(aAeightAndSize)"
                  ></v-radio>
                </v-radio-group>
              </v-flex>
              <v-flex
                sm8
                xs6
              >
                <v-layout
                  row
                  wrap
                >
                  <v-flex xs12>
                    <!--FIXME -->
                    <v-toolbar
                      floating
                      dense
                    >
                      <v-tooltip>
                        <v-btn
                          slot="activator"
                          icon
                          @click.native="select"
                        >
                          <v-icon>fas fa-hand-pointer</v-icon>
                        </v-btn>
                        <span></span>
                      </v-tooltip>
                      <v-tooltip>
                        <v-btn
                          slot="activator"
                          icon
                          @click.native="closeDialog"
                        >
                          <v-icon>fas fa-backward</v-icon>
                        </v-btn>
                        <span></span>
                      </v-tooltip>
                    </v-toolbar>
                  </v-flex>
                  <v-flex xs6>
                    <v-list dense>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                    </v-list>
                    <v-divider />
                  </v-flex>
                  <v-flex xs6>
                    <v-list dense>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.name}}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.movility}}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.evadeRate}}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.armorPoint}}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.constitution}}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.initiative}}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.chargeDamage}}{{machineType.coveredChargeDamage}}</v-list-tile-content>
                      </v-list-tile>
                    </v-list>
                    <v-divider />
                  </v-flex>
                  <v-flex xs6>
                    <v-list dense>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass"></v-list-tile-content>
                      </v-list-tile>
                    </v-list>
                  </v-flex>
                  <v-flex xs6>
                    <v-list dense>
                      <v-list-tile>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.headSlot}}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.bodySlot}}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.rightArmSlot}}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.leftArmSlot}}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.rightLegSlot}}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content :class="contentClass">{{machineType.leftLegSlot}}</v-list-tile-content>
                      </v-list-tile>
                    </v-list>
                  </v-flex>
                </v-layout>
              </v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-card>
    </v-dialog>
  </div>
</template>

<style lang="styl">
.v-label {
  font-size: 11pt;
}
</style>

<script>
import MachineType from "@/model/embriomachine/machinetype";

export default {
  name: "MachineTypeSelectorDialog",
  props: {
    //
    showDialog: {
      type: Boolean,
      default: true
    },
    //null
    targetMachineType: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      //
      seatTypes: ["", ""],
      seatType: "",
      weightAndSizes: MachineType.getWeightAndSize(),
      weightAndSize: MachineType.getWeightAndSize()[0]
    };
  },

  watch: {
    targetMachineType: function(val) {
      if (val != null) {
        this.weightAndSize = val.toWeightAndSize();
      }
    }
  },

  computed: {
    machineType() {
      let ret = {};
      this.machineTypes.forEach(item => {
        if (
          item.size === this.weightAndSize.size &&
          item.weight === this.weightAndSize.weight
        ) {
          ret = item;
        }
      });
      return ret;
    },

    machineTypes() {
      //
      return MachineType.getMachineTypes().filter(machineType => {
        if (this.seatType === "") {
          return machineType.hasDoubleSeat;
        } else {
          return !machineType.hasDoubleSeat;
        }
      });
    },

    contentClass() {
      if (this.$vuetify.breakpoint.name === "xs") {
        return "caption";
      } else {
        return "subheading";
      }
    },

    isXs() {
      return this.$vuetify.breakpoint.name === "xs";
    }
  },

  methods: {
    closeDialog() {
      // this.$emit("update:targetMachineType", {});
      this.$emit("cancel");
      this.weightAndSize = null;
      this.$emit("update:showDialog", false);
    },
    select() {
      // this.$emit("update:targetMachineType", this.machineType);
      this.$emit("select", this.machineType);
      this.weightAndSize = null;
      this.$emit("update:showDialog", false);
    },
    getNameOf(weightAndSize) {
      let name = MachineType.getNameOf(weightAndSize);
      if (this.seatType === "") {
        name += "()";
      }
      return name;
    }
  }
};
</script>



