<template>
  <v-dialog v-model="showDialog">
    <v-card>
      <v-card-title>
        <H2> </H2>
      </v-card-title>
      <v-card-text>
        <p>PC</p>
        <h3></h3>
        <v-divider />
        <h4>1</h4>
        <ul>
          <li></li>
          <li></li>
        </ul>
        <v-divider />
        <h4></h4>
        <ul>
          <li></li>
          <li></li>
          <li></li>
        </ul>
        <v-divider />
        <h4></h4>
        <ul>
          <li></li>
        </ul>
        <v-divider />
        <h4></h4>
        <ul>
          <li>twitter</li>
          <li></li>
          <li>anonymous</li>
        </ul>
        <v-divider />
        <h4></h4>
        <ul>
          <li></li>
        </ul>
        <v-divider />
        <h3></h3>
        <a href="https://twitter.com/minokuba_pub">minokuba_pub</a>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="green darken-1"
          flat
          @click.native="closeDialog"
        ></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<style>
</style>

<script>
export default {
  name: "SpellHelpDialog",
  props: {
    //
    showDialog: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    closeDialog() {
      this.$emit("update:showDialog", false);
    }
  }
};
</script>



