<template>
  <div>
    <v-dialog
      v-model="showDialog"
      :persistent="editMode"
      max-width="1000"
      :fullscreen="isMd"
    >
      <v-layout row>
        <v-card>
          <v-container
            fruid
            grid-list-md
          >
            <v-layout
              row
              wrap
              justify-center
            >
              <v-flex
                sm1
                xs1
              />
              <v-flex
                sm2
                xs3
              >
                <v-select
                  dense
                  label="Select"
                  :items="editions"
                  v-model="selectedEditions"
                  max-height="400"
                  hint=""
                  persistent-hint
                  multiple
                  :disabled="!editMode"
                ></v-select>
              </v-flex>
              <v-flex
                sm2
                xs3
              >
                <v-select
                  dense
                  label="Select"
                  :items="ranks"
                  v-model="selectedRanks"
                  max-height="400"
                  hint=""
                  persistent-hint
                  multiple
                  :disabled="!editMode"
                ></v-select>
              </v-flex>
              <v-flex
                sm2
                xs4
              >
                <v-select
                  dense
                  label="Select"
                  :items="types"
                  v-model="selectedTypes"
                  max-height="400"
                  hint=""
                  persistent-hint
                  multiple
                  :disabled="!editMode"
                ></v-select>
              </v-flex>
              <v-flex sm3>
                <v-text-field
                  v-if="editMode&&!isXs"
                  v-model="searchEquipmentName"
                  label=""
                  item-value="text"
                  :disabled="!editMode"
                ></v-text-field>
              </v-flex>
              <v-flex sm1>
                <v-btn
                  v-if="editMode&&!isXs"
                  label=""
                  :disabled="!editMode"
                  @click.native="selectAll"
                >{{buttonLabel}}
                </v-btn>
              </v-flex>
              <!--FIXME -->
              <v-toolbar
                floating
                dense
                v-if="isXs"
              >
                <v-select
                  :items="itemcounts"
                  v-model="itemcount"
                  label=""
                  item-value="text"
                  :disabled="!editMode"
                ></v-select>
                <v-tooltip>
                  <v-btn
                    slot="activator"
                    icon
                    @click.native="select"
                    :disabled="!editMode"
                  >
                    <v-icon>fas fa-hand-pointer</v-icon>
                  </v-btn>
                  <span></span>
                </v-tooltip>
                <v-tooltip>
                  <v-btn
                    slot="activator"
                    icon
                    @click.native="closeDialog"
                  >
                    <v-icon>fas fa-backward</v-icon>
                  </v-btn>
                  <span></span>
                </v-tooltip>
              </v-toolbar>
              <!-- <v-flex
                sm9
                xs6
              /> -->
              <v-flex
                sm4
                xs5
              >
                <v-layout
                  style="max-height: 600px"
                  class="scroll-y"
                >
                  <v-radio-group
                    label=""
                    v-model="selectedEquipmentName"
                    :mandatory="false"
                    :class="isXs ? 'small' : ''"
                  >
                    <v-radio
                      v-for="equipment in equipments"
                      :label="formatEquipmentName(equipment)"
                      :value="equipment.name"
                      :key="equipment.name"
                      :disabled="!editMode"
                      class="caption"
                    ></v-radio>
                  </v-radio-group>
                </v-layout>
              </v-flex>
              <v-flex
                sm4
                xs3
              >
                <v-list
                  :dense="isXs"
                  :two-line="isXs"
                >
                  <v-list-tile v-if="!isXs">
                    <v-select
                      v-if="editMode"
                      :items="itemcounts"
                      v-model="itemcount"
                      label=""
                      item-value="text"
                    ></v-select>
                  </v-list-tile>
                  <v-list-tile>
                    <v-list-tile-content :class="contentClass"></v-list-tile-content>
                  </v-list-tile>
                  <v-divider />
                  <v-list-tile>
                    <v-list-tile-content :class="contentClass"></v-list-tile-content>
                  </v-list-tile>
                  <v-divider />
                  <v-list-tile>
                    <v-list-tile-content :class="contentClass"></v-list-tile-content>
                  </v-list-tile>
                  <v-divider />
                  <v-list-tile>
                    <v-list-tile-content :class="contentClass"></v-list-tile-content>
                  </v-list-tile>
                  <v-divider />
                  <v-list-tile>
                    <v-list-tile-content :class="contentClass"></v-list-tile-content>
                  </v-list-tile>
                  <v-divider />
                  <v-list-tile>
                    <v-list-tile-content :class="contentClass"></v-list-tile-content>
                  </v-list-tile>
                  <v-divider />
                </v-list>
              </v-flex>
              <v-flex
                sm4
                xs4
              >
                <v-list
                  :dense="isXs"
                  :two-line="isXs"
                >
                  <v-list-tile v-if="!isXs">
                    <v-toolbar floating>
                      <v-tooltip>
                        <v-btn
                          slot="activator"
                          icon
                          @click.native="select"
                          :disabled="!editMode"
                        >
                          <v-icon>fas fa-hand-pointer</v-icon>
                        </v-btn>
                        <span></span>
                      </v-tooltip>
                      <v-tooltip>
                        <v-btn
                          slot="activator"
                          icon
                          @click.native="closeDialog"
                        >
                          <v-icon>fas fa-backward</v-icon>
                        </v-btn>
                        <span></span>
                      </v-tooltip>
                    </v-toolbar>
                  </v-list-tile>
                  <v-list-tile>
                    <v-list-tile-content :class="contentClass">{{equipment.type}}{{equipment.rank}}</v-list-tile-content>
                  </v-list-tile>
                  <v-divider />
                  <v-list-tile>
                    <v-list-tile-content :class="contentClass">{{equipment.range}}</v-list-tile-content>
                  </v-list-tile>
                  <v-divider />
                  <v-list-tile>
                    <v-list-tile-content :class="contentClass">{{formatDamage(equipment)}}</v-list-tile-content>
                  </v-list-tile>
                  <v-divider />
                  <v-list-tile>
                    <v-list-tile-content :class="contentClass">{{equipment.mountPosition}}</v-list-tile-content>
                  </v-list-tile>
                  <v-divider />
                  <v-list-tile>
                    <v-list-tile-content :class="contentClass">{{equipment.formatMinLimit}}</v-list-tile-content>
                  </v-list-tile>
                  <v-divider />
                  <v-list-tile>
                    <v-list-tile-content :class="contentClass">{{equipment.maxLimit}}</v-list-tile-content>
                  </v-list-tile>
                  <v-divider />
                </v-list>
              </v-flex>
              <v-flex xs12>
                <v-card-text class="body-1">
                  <span
                    style="white-space: pre-wrap;display: inline-block;height:30px;"
                    v-html="equipment.effect"
                  ></span>
                </v-card-text>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-layout>
    </v-dialog>
  </div>
</template>

<style>
/* 
   @see https://github.com/vuetifyjs/vuetify/issues/5715
   @see https://codepen.io/jongallant/pen/MLGPVR */
.small label {
  font-size: 12px;
}
</style>

<script>
import Equipment from "@/model/embriomachine/equipment";

export default {
  name: "EquipmentSeletorDialog",
  props: {
    //
    showDialog: {
      type: Boolean,
      default: true
    },
    //null
    targetEquipment: {
      type: Object,
      default: null
    },
    //
    targetPosition: {
      type: String,
      default: null
    },
    //
    editMode: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {
      selectedEquipmentName: this.targetEquipment.name,
      types: [
        Equipment.TYPE_SHAGEKI,
        Equipment.TYPE_HAKUHEI,
        Equipment.TYPE_KIRAI,
        Equipment.TYPE_SOUKOU,
        Equipment.TYPE_HOJO,
        Equipment.TYPE_TOTSUGEKI,
        Equipment.TYPE_IDOU,
        Equipment.TYPE_SONOTA
      ],
      selectedTypes: [Equipment.TYPE_SHAGEKI],
      ranks: ["S", "A", "B"],
      selectedRanks: ["B"],
      itemcounts: [1, 2, 3, 4],
      itemcount: 1,
      editions: ["", ""],
      selectedEditions: [""],
      searchEquipmentName: "",
      buttonLabel: ""
    };
  },

  watch: {
    targetEquipment: function(val) {
      if (val && val.name) {
        this.selectedEquipmentName = val.name;
        this.selectedTypes = val.type.split("");
        this.selectedRanks = [val.rank];
        this.selectedEditions = [val.edition];
        this.searchEquipmentName = "";
      } else {
        this.selectedEquipmentName = "";
      }
    },
    equipment: function(val) {
      this.itemcount = val.calcDefaultItemCount();
    },
    showDialog: function(val) {
      if (!val) {
        this.closeDialog();
      }
    }
  },

  computed: {
    equipments() {
      return Equipment.getEquipments().filter(equipment => {
        let retVal = true;
        retVal = this.selectedTypes.reduce((accumurator, type) => {
          if (!accumurator && !equipment.isTypeOf(type)) {
            return false;
          } else {
            return true;
          }
        }, false);
        retVal =
          retVal &&
          this.selectedRanks.reduce((accumurator, rank) => {
            if (!accumurator && equipment.rank !== rank) {
              return false;
            } else {
              return true;
            }
          }, false);
        retVal =
          retVal &&
          this.selectedEditions.reduce((accumurator, edition) => {
            if (!accumurator && equipment.edition !== edition) {
              return false;
            } else {
              return true;
            }
          }, false);

        if (this.searchEquipmentName !== "") {
          retVal = retVal && equipment.name.includes(this.searchEquipmentName);
        }

        //
        if (this.targetPosition && !equipment.canEquip(this.targetPosition)) {
          retVal = false;
        }
        // console.log(equipment.name + " = " + retVal);
        return retVal;
      });
    },

    equipment() {
      let ret = new Equipment();

      this.equipments.forEach(item => {
        if (item.name === this.selectedEquipmentName) {
          ret = item;
        }
      });
      return ret;
    },

    contentClass() {
      if (this.$vuetify.breakpoint.name === "xs") {
        return "caption";
      } else {
        return "subheading";
      }
    },
    isXs() {
      return this.$vuetify.breakpoint.name === "xs";
    },
    isMd() {
      return ["xs", "sm", "md"].includes(this.$vuetify.breakpoint.name);
    }
  },

  methods: {
    selectAll() {
      if (this.buttonLabel === "") {
        this.selectedTypes = this.types;
        this.selectedRanks = this.ranks;
        this.selectedEditions = this.editions;
        this.buttonLabel = "";
      } else {
        this.selectedTypes = [Equipment.TYPE_SHAGEKI];
        this.selectedRanks = ["B"];
        this.selectedEditions = [""];
        this.buttonLabel = "";
      }
    },
    closeDialog() {
      this.$emit("update:showDialog", false);
      this.$emit("cancel");
      this.itemcount = 1;
    },
    select() {
      this.$emit("update:showDialog", false);
      this.$emit("select", this.equipment, this.itemcount);
      this.itemcount = 1;
    },
    //FIXME deprecated.
    formatEquipmentName(equipment) {
      return equipment.name;
    },
    formatDamage(equipment) {
      if (
        equipment === null ||
        equipment === undefined ||
        equipment.damage === undefined
      ) {
        return "";
      } else {
        return equipment.damage + "(" + equipment.damageType + ")";
      }
    }
  }
};
</script>



