import Card from "@/model/embriomachine/card";

export default class Deck {

  constructor() {
    // 
    this.cards = [];
    this.addAlignment();

    this.deck = []; //
    this.discardTag = []; //
    this.shuffleCount = 0; //
    this.drowCardCount = 0; //
  }

  addEquipments(equipments) {
    equipments.forEach(equipment => {
      if (!equipment.isTypeOf("") || equipment.name == "" || equipment.name =="") {
        let card = new Card(equipment.name, equipment);
        this.addCard(card);
      }
    });
  }

  setMovility(movility) {
    for (let i = 1; i <= movility; i++) {
      let card = new Card("" + i);
      this.addCard(card);
    }
  }

  setArmor(armor) {
    for (let i = 0; i < armor; i++) {
      let card = new Card("");
      this.addCard(card);
    }
  }

  setSize(size) {
    //TODO machineType.chargeCount
    this.addCard(new Card(""));
    if (size === "LL") {
      this.addCard(new Card(""));
    }
  }

  setEvadeRate(evadeRate) {
    this.evadeRate = evadeRate;
  }

  addAlignment() {
    let card = new Card("");
    this.addCard(card);
  }

  addCard(card) {
    this.cards.push(card);
  }

  //
  shuffle(hands) {
    if (hands === undefined) hands = [];

    //1.
    let tmp = this.cards.concat();
    //tmp
    tmp = tmp.filter(t => hands.indexOf(t) === -1);

    //2.
    let deckSize = tmp.length

    //3.
    for (let i = 0; i < deckSize; i++) {
      let random = this.getRandomIntInclusive(0, tmp.length - 1);
      let c = tmp[random];
      this.deck.push(c);
      tmp = tmp.filter(t => t !== c);
    }

    this.shuffleCount++;
  }

  drowCards() {
    let hands = [];
    for (let i = 1; i <= this.evadeRate; i++) {
      if (this.deck.length === 0) {
        this.shuffle(hands);
      }
      hands.push(this.deck.shift());
    }
    this.drowCardCount++;
    return hands;
  }

  // 2 
  getRandomIntInclusive(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min; //The maximum is inclusive and the minimum is inclusive 
  }
}



